.SignIn-Page{
    width: 35%;
    height: 55%;
background-color: #55efc4;
border-radius: 9px;
box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

input{
    width: 70%;
    border: none;
    padding: 9px 19px;
    margin-top: 8px;
    border-radius: 7px;
    margin-left: 40px;
}
button{
    border: none;
    padding: 9px 19px;
    margin-top: 15px;
    margin-left: 40px;
    border-radius: 4px;
    background-color: #0984e3;
    color: white;
    font-weight: bolder;
    font-size: 16px;
}

.mainsignIn{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;



}
